.Menustyle {
  width: 100%;
  height: 100px;
  background: #b88ddb;
}
* {
  margin: 0;
  padding: 0;
}

.Menustyle ul {
  height: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.Menustyle ul li {
  list-style: none;
  font-size: 30px;
  color: white;
}
